import React,{ Component, useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import Grid from '@material-ui/core/Grid';
import TodayRoundedIcon from '@material-ui/icons/TodayRounded';
import DialpadRoundedIcon from '@material-ui/icons/DialpadRounded';
import CardContent from '@material-ui/core/CardContent';
import 'date-fns';
import DateFnsUtils from '@date-io/date-fns';
import FormControl from '@material-ui/core/FormControl';
import InputAdornment from '@material-ui/core/InputAdornment';
import MenuItem from '@material-ui/core/MenuItem';
import TextField from '@material-ui/core/TextField';
import NotesRoundedIcon from '@material-ui/icons/NotesRounded';
import IconButton from '@material-ui/core/IconButton';
import CloseIcon from '@material-ui/icons/Close';

function Barang({ser,handleChange,currencies,insertChange,changeNum,deletes}){
  
  function DeleteButton({res}) {
    const indeks = res.index;
    if (indeks != 1) {
      return (
        <IconButton onClick={(e)=>deletes(res)} color="secondary" style={{padding:'0px'}} aria-label="add an alarm"><CloseIcon /></IconButton>
      );
    }
    return (
      <NotesRoundedIcon />
      );
  }
return (
    <>
    {ser.map((option) => (
            
            <Grid container spacing={2} style={{marginBottom:'10px'}}>
             
          <Grid item>
          <DeleteButton res={option}/>
            
          </Grid>
          <Grid item>
          <TextField
          id="standard-select-currency"
          select
          label="Kode Barang"
          value={option.value}
          onChange={(e)=>handleChange(option.index, e.target.value)}
         style={{minWidth:120}}
         required
        >
          {currencies.map((optiond) => (
            <MenuItem key={optiond.value} value={optiond.value}>
              {optiond.label}
            </MenuItem>
          ))}
        </TextField>
          </Grid>
          <Grid item>
          <TextField InputLabelProps={{
            shrink: true,
          }} required  onChange={(e) => insertChange(option.index, e.target.value)} id="standard-required"   size="small" label="Harga Satuan" value={option.input} variant="filled"  InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <h4>Rp</h4>
              </InputAdornment>
            ),
          }} style={{maxWidth:'200px'}}/>
          </Grid>
          <Grid item>
          <TextField InputLabelProps={{
            shrink: true,
          }} required id="standard-required" onChange={(e) => changeNum(option.index, e.target.value)} type="number" size="small" label="Unit" value={option.quantity} variant="filled" style={{maxWidth:"100px"}}  />
          </Grid>
        </Grid>
          ))}
     
    </>
)
        }
        export default Barang;