import 'date-fns';
import React,{ Component, useEffect, useState, useRef } from 'react';
import Grid from '@material-ui/core/Grid';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import { Card } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
    table: {
      minWidth: 650,
    },
  });
  
  function createData(name, calories, fat, carbs, protein) {
    return { name, calories, fat, carbs, protein };
  }
  
  const rows = [
    createData('Frozen yoghurt', 159, 6.0, 24, 4.0),
    createData('Ice cream sandwich', 237, 9.0, 37, 4.3),
    createData('Eclair', 262, 16.0, 24, 6.0),
    createData('Cupcake', 305, 3.7, 67, 4.3),
    createData('Gingerbread', 356, 16.0, 49, 3.9),
  ];
  function rubah(angka){
    var reverse = angka.toString().split('').reverse().join(''),
    ribuan = reverse.match(/\d{1,3}/g);
    ribuan = "Rp"+ribuan.join('.').split('').reverse().join('');
    return ribuan;
  }
export default function ShowPage({login}) {
    const classes = useStyles();
    const [data, setData] = useState([]);
    const [lastIndex, setlast] = useState(0);
    const [loading, setLoad] = useState(true);
    const [formDisplay, setForm] = useState(false);
    const [orderBy, setorderBy] = useState('petName');
    const [orderDir, setorderDir] = useState('asc');
    const [queryText, setqueryText] = useState('');
    const [filteredApts, setfilteredApts] = useState([]);
    const [selectedDate, setSelectedDate] = useState(new Date());

  const handleDateChange = (date) => {
    setSelectedDate(date);
  };

  useEffect(() => {
    
    async function fetchData(){
        var month = selectedDate.getMonth() + 1;
      
          var day = selectedDate.getDate();
      
          var year = selectedDate.getFullYear();
      
         let tanggal = day + "/" + month + "/" + year;
        const result = await fetch(`/api/nota`,{
            method:'post',
            body:JSON.stringify({
                tanggal:tanggal
            }),
            headers:{
                'Content-Type':'application/json'
            }
        })
      const json = await result.json()
      setData([...json.map((item,index)=>{
        item.id = index;
        setlast(index);
        return item;
      })])
      setfilteredApts([...json.map((item,index)=>{
        item.id = index;
        setlast(index);
        return item;
      })])
      console.log(json)
    }
    fetchData()
    
  }, [login]);
  
  return (
    <>
    <center>
        <Card className={'card-form '} >
        <MuiPickersUtilsProvider utils={DateFnsUtils}>
    <KeyboardDatePicker
          margin="normal"
          id="date-picker-dialog"
          label="Date picker dialog"
          format="MM/dd/yyyy"
          value={selectedDate}
          onChange={handleDateChange}
          KeyboardButtonProps={{
            'aria-label': 'change date',
          }}
        />
        </MuiPickersUtilsProvider>
        </Card>
        <TableContainer component={Paper} style={{marginBottom:'20px'}}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            
            <TableCell ><b>No. Nota</b></TableCell>
            <TableCell ><b>No. Pesanan</b></TableCell>
            <TableCell style={{minWidth:'80px'}}><b>Nama Barang</b></TableCell>
            <TableCell ><b>Harga Satuan</b></TableCell>
            <TableCell ><b>Jumlah Barang</b></TableCell>
            <TableCell ><b>Jumlah Harga</b></TableCell>
            <TableCell ><b>Saldo</b></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {filteredApts.map((row) => (
            <TableRow key={row.id}>
              <TableCell component="th" scope="row">
                {row.nota}
              </TableCell>
              <TableCell >{row.pesanan}</TableCell>
              <TableCell >
                  {
                      row.barang.map((rows)=>(
                        <p style={{margin:'0px',lineHeight:'30px'}}>{rows.value}</p>
                      ))
                  }
                  
              </TableCell>
              <TableCell >{
                      row.barang.map((rows)=>(
                        <p style={{margin:'0px',lineHeight:'30px'}}>{rubah(Number(rows.satuan))}</p>
                      ))
                  }</TableCell>
              <TableCell >
              {row.barang.map((rows)=>(
                        <p style={{margin:'0px',lineHeight:'30px'}}>{rows.quantity}</p>
                      ))}
              </TableCell>
              <TableCell >
              {row.barang.map((rows)=>(
                        <p style={{margin:'0px',lineHeight:'30px'}}>{rubah((Number(rows.satuan)*Number(rows.quantity)))}</p>
                      ))}
              </TableCell>
              <TableCell >{rubah(Number(row.total))}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
    </center>
    </>
  )
}

